import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
  padding: 20px;
`;
let 제목 = styled.h4`
  font-size: 25px;
  color: ${props => props.색상}
`;

function Detail(props){

  let [alert, setAlert] = useState(true); //현재 보입니다.
  let [inputData, setInputData] = useState('');

  useEffect(()=>{
    //2초 후에 alert 창을 안보이게 해주세요
    let timer = setTimeout(()=>{ setAlert(false) },2000);

    console.log('안녕')
    //return function 어쩌구(){실행할 코드} //사라질 때 실행됨
    return ()=>{clearTimeout(timer)}
  },[]);
 

  let { id } = useParams();
  let history = useHistory();

  // let matchItem = props.shoes.find(function(item){
  //   return item.id == id
  // })
  
  let matchItem = props.shoes.find(x => x.id == id);

  return (
    <div className="container">
      <박스>
        <제목 색상="pink" className="red">Detail</제목>
        <제목 색상={'green'}>Detail2</제목>
      </박스>

      { inputData }
      <input type="text" onChange={(e)=>{ setInputData(e.target.value) }} />
      {
        alert
        ? (<div className="my-alert my-alert2">
              <p>재고가 얼마 남지 않았습니다.</p>
            </div>)
        : null
      }
      
      
      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" alt=""  width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{matchItem.title}</h4>
          <p>{matchItem.content}</p>
          <p>{matchItem.price}원</p>


          <Info stock={props.stock} />

          <button className="btn btn-danger" onClick={()=>{
            props.setStock([9,10,11])
          }}>주문하기</button>
          &nbsp;
          <button className="btn btn-danger" onClick={()=>{
            history.goBack();
          }}>뒤로가기</button>
        </div>
      </div>
    </div>
  )
}

function Info(props){
  return (
    <p>재고 : {props.stock[1]}</p>
  )
}
export default Detail;